import { ThemeContext } from './Contexts/ThemeContext'
import { useState } from 'react';
import './App.scss';
import Navbar from './Modules/Navbar/Navbar';
import MainContent from './Modules/MainContent/MainContent';

function App() {
  const [darkMode, setDarkMode] = useState(false);

  const toggleTheme = () => {
    setDarkMode(!darkMode);
  }

  return (
    <div className={`tw-flex tw-flex-col ${darkMode ? 'tw-dark' : ''}`}>
      <ThemeContext.Provider value={{darkMode: darkMode, toggleTheme: toggleTheme}}>
        <Navbar/>
        <div className="tw-overflow-y-auto" style={{maxHeight: 'calc(100vh - 46px)'}}>
          <MainContent darkMode={darkMode}/>
        </div>
      </ThemeContext.Provider>
    </div>
  );
}

export default App;
