import React from 'react'
import './SingleProject.scss'
import { ProjectDetails } from './ProjectList'

export default function SingleProject(props: { details: ProjectDetails, setProject: (project: ProjectDetails) => void }) {
    return (
        <div className="tw-relative tw-flex">
            <a href="#Projects"><img src={`/Assets/ProjectIcons/${props.details.icon_image}`}
                className="tw-w-32 tw-h-32 tw-object-contain project-icon"
                onClick={() => props.setProject(props.details)}
            ></img></a>
        </div>
    )
}
