import React from 'react'
import About from './About/About'
import Contact from './Contact/Contact'
import './MainContent.scss'
import ProfileCard from './ProfileCard/ProfileCard'
import Projects from './Projects/Projects'

export default function MainContent(props: { darkMode: boolean }) {
    return (
        <div className={`tw-transition-colors
            tw-bg-gradient-to-bl tw-from-blue-500 tw-via-green-300 tw-to-green-500
            dark:tw-from-gray-800 dark:tw-via-gray-800 dark:tw-to-gray-800 dark:tw-text-white
            tw-flex tw-flex-col tw-items-center`}>
                <div className="tw-m-20 tw-w-full md:tw-w-max" id="Profile">
                    <ProfileCard></ProfileCard>
                </div>
                <div className="tw-m-20 tw-w-full" id="About">
                    <About></About>
                </div>
                <div className="tw-m-20 tw-w-full lg:tw-w-2/3" id="Projects">
                    <Projects></Projects>
                </div>
                <div className="tw-m-20 tw-w-full" id="Contact">
                    <Contact></Contact>
                </div>
        </div>
    )
}
