import React from 'react'
import { ProjectDetails, WebTechnologies } from './ProjectList'
import { AiFillCloseCircle, SiElectron, FaAngular, FaReact, SiTypescript, FaNodeJs, SiTailwindcss, SiJavascript, ImHtmlFive, SiCss3 } from 'react-icons/all'

function getIconFromName(iconName: WebTechnologies) {
    switch (iconName) {
        case WebTechnologies.Angular:
            return <FaAngular></FaAngular>
        case WebTechnologies.React:
            return <FaReact></FaReact>
        case WebTechnologies.Electron:
            return <SiElectron></SiElectron>
        case WebTechnologies.TypeScript:
            return <SiTypescript></SiTypescript>
        case WebTechnologies.Node:
            return <FaNodeJs></FaNodeJs>
        case WebTechnologies.TailwindCSS:
            return <SiTailwindcss></SiTailwindcss>
        case WebTechnologies.JavaScript:
            return <SiJavascript></SiJavascript>
        case WebTechnologies.HTML:
            return <ImHtmlFive></ImHtmlFive>
        case WebTechnologies.CSS:
            return <SiCss3></SiCss3>
        default:
            return <span>{iconName}</span>
    } 
}

export default function ProjectDetail(props: { project: ProjectDetails | null, resetProject: () => void }) {

    return (
        <div className="tw-flex tw-flex-col md:tw-flex-row md:tw-p-2">
            <div className="tw-flex tw-flex-col tw-w-full md:tw-w-1/3 tw-justify-center tw-items-center
                ">
                <img src={`/Assets/ProjectIcons/${props.project?.icon_image}`}
                    className="tw-w-40 tw-h-40 tw-object-contain"></img>
                <div className="font-open-sans tw-text-2xl tw-font-bold tw-m-2 tw-text-center">{props.project?.title}</div>
            </div>
            <div className="tw-border-b-2 md:tw-border-l-2 tw-border-green-500 dark:tw-border-green-500 tw-mx-5"></div>
            <div className="tw-w-full tw-flex tw-flex-col tw-justify-center tw-px-5 tw-py-2 md:tw-px-0">
                <span className="tw-font-semibold">Organization: <span className="tw-font-normal">{props.project?.organization} </span></span>
                <span className="tw-flex tw-items-center tw-font-semibold">Technologies: <span  className="tw-flex tw-text-xl tw-font-normal">
                    {props.project?.technologies_used.map((tech) =>
                        <span className="tw-mx-1">{getIconFromName(tech)}</span>)
                    }</span>
                </span>
                <span className="tw-font-semibold">Description: <span className="tw-font-normal">{props.project?.description}</span></span>
                <span className="tw-font-semibold">Link:
                    <a href={props.project?.link} className="tw-font-normal hover:tw-text-green-500" target="_blank" rel="noreferrer"> {props.project?.link}</a>
                </span>
            </div>
            <div>
                <span className="tw-hidden md:tw-inline-block tw-text-2xl tw-cursor-pointer">
                    <AiFillCloseCircle onClick={() => props.resetProject()}></AiFillCloseCircle>
                </span>
                <span className="md:tw-hidden tw-flex tw-justify-center tw-mx-5 tw-my-2 tw-p-2 tw-cursor-pointer
                 tw-transition-colors tw-bg-green-600 hover:tw-bg-green-800 dark:tw-bg-gray-800 dark:hover:tw-bg-gray-500 tw-text-white tw-rounded"
                    onClick={() => props.resetProject()}>
                    Cancel
                </span>
            </div>
        </div>
    )
}
