import { ThemeContext } from "../../../Contexts/ThemeContext";
import Avatar from "../Avatar/Avatar";

export default function ProfileCard() {
    return (
        <div className="tw-transition-colors md:tw-inline-flex tw-justify-center tw-bg-white md:tw-rounded dark:tw-bg-gray-900
            tw-p-5">
            <Avatar></Avatar>
            <div className="tw-border-b-2 md:tw-border-l-2 tw-border-green-500 dark:tw-border-green-500 tw-mx-5"></div>
            <div className="tw-p-5 md:tw-max-w-md tw-flex tw-flex-col tw-justify-between">
                <h1 className="tw-py-2 tw-text-3xl tw-font-semibold font-poppins tw-text-green-500 dark:tw-text-green-500">Hello!</h1>
                <p>Welcome to my portfolio!</p>
                <p>
                    Scroll down to see different sections of the website containing different information,
                    or just use the menu above if you are not on a mobile!
                </p>
                <p>
                    My contact information can be found to the left.
                    There are links to my GitHub profile, LinkedIn profile, and my email.
                </p>
                <ThemeContext.Consumer>
                    {({darkMode,}) => (
                        <p>
                            Use the button on the top right corner to turn {darkMode ? 'off' : 'on'} the dark mode.
                        </p>
                    )}
                </ThemeContext.Consumer>
            </div>
        </div>
    )
}
