import React, { useState } from 'react'
import ProjectDetail from './ProjectDetail'
import { ProjectDetails, projectList } from './ProjectList'
import SingleProject from './SingleProject'

export default function Projects() {
    const [selectedProject, setselectedProject] = useState<ProjectDetails | null>(null)

    return (
        <div className="tw-transition-colors tw-bg-white tw-text-black dark:tw-bg-gray-900 dark:tw-text-white
        tw-flex tw-flex-col
        tw-p-5 md:tw-rounded">
            <div className="tw-text-center">
                <span className="tw-text-4xl tw-font-semibold font-poppins tw-text-green-500 dark:tw-text-green-500 tw-m-5">Projects</span>
            </div>
            {
                selectedProject ?
                <div className="tw-m-5 tw-p-3 tw-rounded tw-shadow-md tw-border" id="ProjectDetail">
                    <ProjectDetail project={selectedProject} resetProject={() => { setselectedProject(null) }}></ProjectDetail>
                </div>
                :
                ''
            }
            <div className="tw-m-5">
                <div className="tw-grid tw-grid-cols-1 md:tw-grid-cols-3 tw-gap-4">
                    {
                        projectList.map(project =>
                            <div className="tw-mx-8 tw-flex tw-justify-center">
                                <SingleProject
                                    details={project}
                                    setProject={(project: ProjectDetails | null) => setselectedProject(project)}
                                ></SingleProject>   
                            </div>
                        )
                    }
                </div>
            </div>
        </div>
    )
}
