import DarkModeButton from "../DarkMode/DarkModeButton";
import Menu from "./Menu/Menu";

export default function Navbar() {
	return (
		<div className="tw-transition-colors tw-bg-white dark:tw-bg-gray-900 dark:tw-text-white
		tw-w-full tw-flex tw-justify-between tw-items-center tw-py-2 tw-px-4
		tw-border-b-2 tw-border-green-500">
			<div className="tw-hidden md:tw-block tw-w-full">
                <Menu></Menu>
            </div>
			<div className="tw-flex tw-justify-end tw-w-full">
				<DarkModeButton></DarkModeButton>
			</div>
		</div>
	)
}
